.carousel {
  position: relative;
  margin: 0 auto;

  &__list {
    display: flex;
    align-items: stretch;
  }

  &__item {
    position: relative;

    background-color: $grey-3;
    color: $dark-2;
    padding: 24px;

    &:nth-child(odd)::after {
      content: "";
      display: block;
      position: absolute;
      width: 4px;
      height: 96%;
      right: -5px;
      top: 2%;
      z-index: 1;
      background: url(../images/dots-line.svg);
    }
  }

  &__item-logo {
    margin-bottom: 40px;
  }

  &__item-text {
    line-height: 34px;
    margin-bottom: 20px;
  }

  &__item-name {
    font-weight: 700;
    margin-bottom: 7px;
  }

  &__item-position {
    font-size: 17px;
    line-height: 28px;
  }

  &__item-firm {
    color: $accent-color;
    font-weight: 600;
  }

  &__quotes {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 78px;
    height: 64px;
    color: $grey-2;
    font-family: Impact, sans-serif;
    font-size: 250px;
    line-height: 82px;
  }

  &__btn {
    position: absolute;
    width: 130px;
    height: 130px;
    top: 50%;
    transform: translateY(-50%);

    background-color: $grey-2;
    transition: background-color 0.5s ease;
    z-index: 1;

    &:visited,
    &:active {
      color: inherit;
    }

    &:focus,
    &:hover {
      background-color: $accent-color;
    }

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 22px;
      height: 35px;
      fill: $white;
    }
  }

  &__btn-next {
    right: -130px;

    &::before {
      background: url(../images/arrow_right.png) no-repeat center;
    }
  }

  &__btn-prev {
    left: -130px;

    &::before {
      background: url(../images/arrow_left.png) no-repeat center;
    }
  }
}

@media screen and (max-width: 1770px) {
  .carousel {
    max-width: 930px;
  }
}

@media screen and (max-width: 1490px) {
  .carousel {
    max-width: 750px;
    background-color: $grey-3;

    &__item:nth-child(odd)::after {
      display: none;
    }

    &::after {
      content: "";
      display: block;
      bottom: 0;
      width: 100%;
      height: 150px;
      background-color: $grey-3;
    }

    &__btn {
      top: inherit;
      bottom: 0;
      transform: translateY(0);
    }

    &__btn-prev {
      left: 0;
    }

    &__btn-next {
      right: 0;
    }
  }
}
