.nav {

  &__list {
    display: flex;
  }

  &__list--mob {
    flex-direction: column;
  }

  &__item {
    margin-right: 45px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__item--mob {
    margin-right: 0;
  }

  &__link {
    padding-bottom: 45px;
    color: $white;

    font-size: 22px;
    font-weight: 400;
    line-height: 34px;
    text-transform: uppercase;

    border-bottom: 6px solid transparent;
    transition: border-color 0.5s ease, color 0.5s ease;

    &:visited,
    &:active {
      color: $white;
    }

    &:focus,
    &:hover {
      color: $dark-1;
      border-color: $dark-1;

    }

  }

  &__link--mob {
    padding-bottom: 10px;
    margin-bottom: 10px;
  }

  &__mobile {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    display: flex;
    justify-content: center;
    padding-top: 150px;

    background-color: $accent-color;
    z-index: 1;

    transform: translateX(-1000px);
    transition: transform 0.6s ease;

  }

  &__mobile--open {
    transform: translateX(0);
  }
}

@media screen and (max-width: 1024px) {
  .nav {

    &__item {
      margin-right: 20px;

      &:last-child {
        margin-right: 0;
      }
    }

    &__link {
      font-size: 18px;
    }
  }
}

@media screen and (max-width: 820px) {
  .nav {

    &__header {
      display: none;
    }
  }
}

@media screen and (min-width: 820px) {
  .nav {

    &__mobile {
      display: none;
    }
  }
}
