.clients {
  position: relative;

  &__container {
    max-width: 1920px;
    margin: 0 auto;
    position: relative;
  }

  &__wrapper {
    max-width: 1200px;
    margin: 0 auto;
    padding-top: 200px;
    padding-bottom: 145px;
  }

  &__title {
    position: relative;
    margin-bottom: 45px;
    padding-bottom: 25px;

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      display: block;
      height: 6px;
      width: 117px;
      background-color: $accent-color;
    }
  }
}

@media screen and (max-width: 1120px) {
  .clients {

    &__wrapper {
      padding-left: 30px;
      padding-right: 30px;
    }
  }
}
