.burger {
  display: none;
  cursor: pointer;
  z-index: 2;

  &__arrow {
    position: relative;
    display: block;
    width: 22px;
    height: 4px;

    background-color: $white;
    transition: all 0.3s ease-in-out;

    &:before,
    &:after {
      content: "";
      position: absolute;
      width: 22px;
      height: 4px;

      background-color: $white;
      transition: all 0.3s ease-in-out;
    }

    &:before {
      top: -8px;
    }

    &:after {
      top: 8px;
    }
  }

  &__arrow--open {
    background-color: transparent;

    &:before {
      transform: rotate(45deg) translate(5px, 6px);
    }

    &:after {
      transform: rotate(-45deg) translate(5px, -6px);
    }
  }

}

@media screen and (max-width: 820px) {
  .burger {
    display: flex;
    align-items: center;
  }
}
