.header {
  height: 1040px;
  background-color: $accent-color;
  // background: url(../images/header-layear.png) no-repeat right/contain;

  &__container {
    position: relative;
    max-width: 1920px;
    margin: 0 auto;
    height: 100%;
  }

  &__top {
    position: relative;
    display: flex;
    justify-content: space-between;

    padding: 30px 50px 0;
    margin-bottom: 100px;
  }

  &__logo {
    position: relative;
    z-index: 2;
  }

  &__logo--part {
    position: absolute;
    top: 11px;
  }

  &__logo-link {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }

  &__content {
    padding-left: 360px;
    max-width: 504px;
    position: relative;
  }

  &__title {
    position: relative;
    padding-bottom: 45px;
    margin-bottom: 45px;
    color: $white;

    font-family: Impact, sans-serif;
    font-size: 75px;
    font-weight: 400;
    line-height: 82px;
    text-transform: uppercase;

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      display: block;
      width: 117px;
      height: 6px;
      background-color: $dark-1;
    }
  }

  &__text {
    max-width: 373px;
    color: $white;
    font-size: 32px;
    font-weight: 400;
    line-height: 40px;
  }

  &__arrows {
    position: absolute;
    bottom: 0;
    left: 22%;
  }
}

@media screen and (max-width: 1024px) {
  .header {

    &__content {
      padding-left: 150px;
    }

    &__title {
      font-size: 45px;
      line-height: 55px;
    }

    &__text {
      max-width: 233px;
      font-size: 22px;
    }

    &__arrows {
      left: 0;
    }
  }
}

@media screen and (max-width: 580px) {
  .header {
    height: 568px;

    &__top {
      padding: 9px 18px 0;
      margin-bottom: 41px;
    }

    &__logo--img {
      max-width: 137px;
    }

    &__logo--part {
      max-width: 60px;
      top: 6px;
    }

    &__content {
      padding-left: 18px;
    }

    &__title {
      font-size: 33px;
      line-height: 38px;
      max-width: 222px;
      padding-bottom: 22px;
      margin-bottom: 16px;
    }

    &__text {
      font-size: 20px;
      line-height: 25px;
    }

    &__arrows {
      max-width: 164px;
      left: -30px;
    }
  }
}
