.toolbar {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-20%);

  &__title {
    padding-right: 20px;
    font-weight: bold;
    line-height: 0;
    text-transform: uppercase;
  }

  &__link {
    color: transparent;
    transition: color 0.5s ease;
  }

  &__box {
    position: relative;
    width: 48px;
    height: 49px;
    background-color: $white;
    transition: background-color 0.5s ease;
  }

  &__img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: filter 0.5s ease;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-bottom: 32px;

    &:hover {
      cursor: pointer;
    }

    &:focus,
    &:hover .toolbar__box {
      background-color: $accent-color;
    }

    &:hover .toolbar__link {
      color: $dark-3;
    }

    &:hover .toolbar__img {
      filter: brightness(0) invert(1);
    }
  }
}

@media screen and (max-width: 1024px) {
  .toolbar {
    display: none;
  }
}
